import React, { useState, useEffect, useCallback, useRef } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { Form, Button, Row, Col } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import XLSX from "xlsx";
import Dialog from "@mui/material/Dialog";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import FormData from "form-data";
import CircularProgress from "@mui/material/CircularProgress";
import DialogTitle from "@mui/material/DialogTitle";
import { Next } from "react-bootstrap/esm/PageItem";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

const EXTENSIONS = ["csv", "xls", "xlsx"];

function HandoverNewTasks(props) {
  let [rates, setRates] = useState([]);
  let [headersConfig, setHeadersConfig] = useState([]);
  let [alart, setAlart] = useState("");
  let [projects, setProjects] = useState([]);
  let [projectsData, setProjectsData] = useState([]);
  let [subProjects, setSubProjects] = useState([]);
  let [projectID, setProjectID] = useState(0);
  let [assignedProjectManager, setAssignedProjectManager] = useState("");
  let [severity, setSeverity] = useState("");
  let [properties, setProperties] = useState("");
  let [tasks, setTasks] = useState([]);
  let [filterTasks, setFilterTasks] = useState([]);
  let [selectedFile, setSelectedFile] = useState([]);

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  let [form, setForm] = useState({});
  let [errors, setErrors] = useState({});
  let [open, setOpen] = useState(false);
  let [openDialog, setOpenDialog] = useState(false);
  let ref = useRef();

  let setField = (field, value) => {
    setForm({
      ...form,
      [field]: value,
    });
    if (!!errors[field])
      setErrors({
        ...errors,
        [field]: null,
      });
  };

  const findFormErrors = () => {
    const { project, subProject, taskAssignedFromDiV, taskAssignedDiv } = form;
    const newErrors = {};
    // name errors
    if (!project || project === "" || taskAssignedFromDiV === "Choose...")
      newErrors.project = "cannot be blank!";
    if (!subProject || subProject === "" || taskAssignedFromDiV === "Choose...")
      newErrors.subProject = "cannot be blank!";
    if (
      !taskAssignedFromDiV ||
      taskAssignedFromDiV === "" ||
      taskAssignedFromDiV === "Choose..."
    )
      newErrors.taskAssignedFromDiV = "Choose a division!";
    if (
      !taskAssignedDiv ||
      taskAssignedDiv === "" ||
      taskAssignedDiv === "Choose..."
    )
      newErrors.taskAssignedDiv = "Choose a division!";
    if (!headersConfig || headersConfig.length === 0)
      newErrors.file = "Attachment missing!";
    return newErrors;
  };
  const findDownloadErrors = () => {
    const { project, subProject, taskAssignedFromDiV, taskAssignedDiv } = form;
    const newErrors = {};
    // name errors
    if (!project || project === "" || taskAssignedFromDiV === "Choose...")
      newErrors.project = "cannot be blank!";
    if (!subProject || subProject === "" || taskAssignedFromDiV === "Choose...")
      newErrors.subProject = "cannot be blank!";
    return newErrors;
  };

  useEffect(() => {
    axiosInstance
      .get("projonline/project", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setForm({});

        setProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.project)),
        ]);
        setSubProjects([
          ...new Set(res.data.projTemplates.map((ele) => ele.subProject)),
        ]);
        setProjectsData(res.data.projTemplates);
        const data = res.data.tasks.filter((task) => {
          if (task.taskStatus != "Withdraw") {
            return task;
          }
        });
        setTasks(data);
        setFilterTasks(data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const convertToJson = (headers, fileData) => {
    const rows = [];
    fileData.forEach((row, i) => {
      let rowData = {};
      rowData["id"] = i;
      row.forEach((element, index) => {
        rowData[headers[index]] = element;
      });
      rows.push(rowData);
    });
    return rows;
  };

  const changeProject = (e) => {
    setField("project", e.target.value);
    // console.log(e.target.value)
    // console.log(projectsData)
    // console.log(projectsData)
    let t = projectsData.filter((task) => task.project == e.target.value);
    setSubProjects([...new Set(t.map((ele) => ele.subProject))]);
    // console.log(t)
    for (var i = 0; i < projectsData.length; i++) {
      if (
        e.target.value == projectsData[i].project &&
        form.subProject == projectsData[i].subProject
      ) {
        // console.log(projectsData[i])
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i].projectID)

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
      }
    }
  };

  const changeSubProject = (e) => {
    // console.log(filterTasks)
    setField("subProject", e.target.value);
    // console.log(e.target.value)

    for (var i = 0; i < projectsData.length; i++) {
      if (
        form.project == projectsData[i].project &&
        e.target.value == projectsData[i].subProject
      ) {
        setAssignedProjectManager(projectsData[i].assignedProjectManager);
        setProjectID(projectsData[i].projectID);
        // console.log(projectsData[i])

        i = projectsData.length;
      } else if (i == projectsData.length - 1) {
        setProjectID("");
        setAssignedProjectManager("");
      }
    }
  };

  // useEffect(()=>{
  //     if(form['project'] && form['subProject']){
  //         setFilterTasks(tasks.filter((task)=>(task.project==form['project'] && task.subProject == form['subProject'])) )
  //     }
  // },[form['project'],form['subProject']])

  const getExtention = (file) => {
    const parts = file.name.split(".");
    const extension = parts[parts.length - 1];
    return EXTENSIONS.includes(extension);
  };

  const importExcel = (e) => {
    const file = e.target.files[0];

    setErrors({
      ...errors,
      file: null,
    });

    setSelectedFile(e.target.files);

    const reader = new FileReader();
    reader.onload = (e) => {
      //parse data
      const bstr = e.target.result;
      const workBook = XLSX.read(bstr, { type: "binary" });

      //get First Sheet
      const workSheetName = workBook.SheetNames[0];
      const workSheet = workBook.Sheets[workSheetName];
      //convert to array
      const fileData = XLSX.utils.sheet_to_json(workSheet, { header: 1 });
      const header = fileData[0];
      const headers = [];
      for (let j = 0; j < projectsData.length; j++) {
        // console.log(projectsData[j].projectID, "_", projectID)

        if (projectsData[j].projectID === projectID) {
          if (arrayEquals(projectsData[j].properties, header)) {
            // console.log(projectsData[j])
            for (var i = 0; i < fileData[0].length; i++) {
              // console.log("rates")
              let element = {
                field: fileData[0][i],
                headerName: fileData[0][i],
                minWidth: 150,
                flex: 1,
              };
              headers.push(element);
            }

            setHeadersConfig(headers);

            fileData.splice(0, 1);
            setProperties(fileData);
            setRates(convertToJson(header, fileData));
            // console.log(fileData)
            //totalInputHeader = projects[j].properties.splice(0, 3)
            j = projectsData.length;
          } else {
            setOpen(true);
            setSeverity("error");
            setAlart("Project Template is different");
            setRates([]);
            setHeadersConfig([]);
            j = projectsData.length;
            //totalInputHeader = projects[j].properties.splice(0, 3)
          }
        } else if (j == projectsData.length - 1) {
          setOpen(true);
          setSeverity("error");
          setAlart("Project Supproject combination is not available");
          setRates([]);
          setHeadersConfig([]);
        }
      }
    };
    if (file) {
      if (getExtention(file)) {
        reader.readAsBinaryString(file);
      } else {
        alert("Invalid file");
      }
    } else {
      setRates([]);
    }
  };

  const onSubmit = (e) => {
    e.preventDefault();
    const newErrors = findFormErrors();
    // Conditional logic:
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
      console.log(newErrors);
    } else {
      var uploadfiles = new FormData();

      uploadfiles.append("files", selectedFile[0]);
      // console.log(selectedFile[0])
      // console.log(uploadfiles)

      const taskObject = {
        taskAssignedFromDiV: form.taskAssignedFromDiV,
        taskAssignedDiv: form.taskAssignedDiv,
        assignedProjectManager: assignedProjectManager._id,
        project: form.project,
        subProject: form.subProject,
        projectID: projectID,
        taskStatus: "Task handed over",
        properties: properties,
        priority: form.priority,
        headerProperties: headersConfig.map((item) => item.field),
      };

      // console.log(taskObject)
      setOpenDialog(true);

      axiosInstance
        .post("projonline/upload", uploadfiles, {
          headers: {
            token:
              "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
            "Content-type": "multipart/form-data",
          },
        })
        .then((res1) => {
          taskObject.handoverFiles = res1.data;
          // console.log(taskObject)
          axiosInstance
            .post("projonline/addTask", taskObject, {
              headers: {
                token:
                  "Bearer " +
                  JSON.parse(localStorage.getItem("user")).accessToken,
              },
            })
            .then((res) => {
              // console.log("Test")
              // console.log(res.data.msg)

              setTimeout(() => {
                setAlart(res.data.msg);
                setSeverity(res.data.variant);
                setForm({
                  taskAssignedFromDiV: "",
                  taskAssignedDiv: "",
                  project: "",
                  subProject: "",
                  priority: "None",
                });
                setProjectID("");
                setAssignedProjectManager("");
                setOpen(true);
              }, 2000);
              setTimeout(() => {
                setOpenDialog(false);
                setOpen(false);
                setRates([]);
                setHeadersConfig([]);
                ref.current.value = "";
              }, 5000);
            })
            .catch((error) => {
              console.log(error);
            });
        })
        .catch((error) => {
          console.log(error);
        });
    }
  };

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setOpen(false);
  };

  const downloadHandover = (e) => {
    const newErrors = findDownloadErrors();
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else if (filterTasks.length > 0) {
      // if (!projectID == '' ){
      //Create Downloadable excel file
      var workbook = XLSX.utils.book_new();

      //get selected projects
      // var selectedProjects = [...new Set(props.location.data.map(ele=>ele.projectID))]
      var worksheetData = [];
      var filteredTasks = tasks.filter(
        (task) =>
          task.project == form["project"] &&
          task.subProject == form["subProject"]
      );
      var data = [];
      // console.log(filteredTasks)
      for (var i = 0; i < filteredTasks.length; i++) {
        let prop = filteredTasks[i].properties;
        // console.log(prop)
        // console.log(filteredTasks[i].taskRef)
        prop.unshift(filteredTasks[i].taskRef);
        worksheetData.unshift(prop);
        //console.log(worksheetData)
      }
      let headerProp = filteredTasks[0].headerProperties;
      headerProp.unshift("Task ID");
      worksheetData.unshift(headerProp);
      var ws = XLSX.utils.aoa_to_sheet(worksheetData);
      XLSX.utils.book_append_sheet(workbook, ws, "sheet1");
      //console.log(worksheetData)
      XLSX.writeFile(workbook, "Already handedover tasks.xlsx");
      // console.log(filteredTasks)
      worksheetData = [];
      filteredTasks = [];
      headerProp = [];
      setTasks(filterTasks);
    } else {
      setOpen(true);
      setSeverity("error");
      setAlart("Project Supproject combination is not available");
    }
  };

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        {/* Content Header (Page header) */}
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div className="row mb-2">
                <div className="col-sm-6">
                  <h1>Handover New Tasks</h1>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>
        {/* /.content-header */}
        {/* Main content */}
        <section className="content">
          <div className="container-fluid">
            <Form onSubmit={onSubmit}>
              <Row className="mb-3">
                <Form.Group as={Col}>
                  <Form.Label>From: Division</Form.Label>
                  <Form.Control
                    onChange={(e) =>
                      setField("taskAssignedFromDiV", e.target.value)
                    }
                    value={form.taskAssignedFromDiV}
                    as="select"
                    defaultValue="Choose..."
                    isInvalid={!!errors.taskAssignedFromDiV}
                  >
                    <option>Choose...</option>
                    <option>Planning_Radio</option>
                    <option>Planning_Core</option>
                    <option>Planning_TX</option>
                    <option>Planning_IBS</option>
                    <option>Planning_WiFi</option>
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.taskAssignedFromDiV}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Assign to: Division</Form.Label>
                  <Form.Control
                    onChange={(e) =>
                      setField("taskAssignedDiv", e.target.value)
                    }
                    value={form.taskAssignedDiv}
                    as="select"
                    defaultValue="Choose..."
                    isInvalid={!!errors.taskAssignedDiv}
                  >
                    <option>Choose...</option>
                    <option>Project_Radio</option>
                    <option>Project_Core</option>
                    <option>Project_TX</option>
                    <option>Project_IBS</option>
                    <option>Project_WiFi</option>
                  </Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.taskAssignedDiv}
                  </Form.Control.Feedback>
                </Form.Group>

                <Form.Group as={Col}>
                  <Form.Label>Project Manager</Form.Label>
                  <Form.Control
                    disabled
                    value={assignedProjectManager.name}
                    className=""
                    type="text"
                    placeholder="Project Manager"
                    isInvalid={!!errors.assignedProjectManager}
                  ></Form.Control>
                  <Form.Control.Feedback type="invalid">
                    {errors.taskAssignedDiv}
                  </Form.Control.Feedback>
                </Form.Group>
              </Row>

              <Row className="mb-3">
                <Col sm={4}>
                  <Form.Group>
                    <Form.Label>Project</Form.Label>
                    <Form.Control
                      onChange={changeProject}
                      value={form.project}
                      as="select"
                      defaultValue="Choose..."
                      isInvalid={!!errors.project}
                    >
                      <option value="">Choose...</option>
                      {projects.map((opt) => (
                        <option value={opt}>{opt}</option>
                      ))}
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                      {errors.project}
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={4}>
                  <Form.Group>
                    <Form.Label>Sub Project</Form.Label>
                    <Form.Control
                      onChange={changeSubProject}
                      disabled={form.project ? false : true}
                      value={form.subProject}
                      as="select"
                      defaultValue="Choose..."
                      isInvalid={!!errors.subProject}
                    >
                      <option>Choose...</option>
                      {subProjects.map((opt) => (
                        <option value={opt}>{opt}</option>
                      ))}
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                      {errors.subProject}
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={2}>
                  <Form.Group>
                    <Form.Label>Project ID</Form.Label>
                    <Form.Control
                      disabled
                      value={projectID}
                      className=""
                      type="text"
                      placeholder="Project ID"
                      isInvalid={!!errors.projectID}
                    />
                    <Form.Control.Feedback type="invalid">
                      {errors.projectID}
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={2}>
                  <Form.Group>
                    <Form.Label>Priority</Form.Label>
                    <Form.Control
                      onChange={(e) => setField("priority", e.target.value)}
                      disabled={form.project ? false : true}
                      value={form.priority}
                      as="select"
                      defaultValue="None"
                      isInvalid={!!errors.priority}
                    >
                      <option>None</option>
                      <option>High Priority</option>
                      <option>VIP</option>
                    </Form.Control>
                    <Form.Control.Feedback type="invalid">
                      {errors.priority}
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>

              <Row className="mb-3">
                <Form.Group as={Col}>
                  <Form.Label>Upload new task handover</Form.Label>
                  <Form.Control
                    onChange={importExcel}
                    ref={ref}
                    type="file"
                    isInvalid={!!errors.file}
                  />
                  <Form.Control.Feedback type="invalid">
                    {errors.file}
                  </Form.Control.Feedback>
                </Form.Group>
              </Row>

              <Row className="mb-3">
                <Button variant="primary" type="submit">
                  Handover new tasks
                </Button>
                <Col>
                  {" "}
                  <Button variant="outline-success" onClick={downloadHandover}>
                    Dowanload already handed over tasks
                  </Button>
                </Col>
              </Row>
            </Form>

            <div style={{ height: 610, width: "100%" }}>
              <DataGrid
                getRowId={(item) => item.id}
                rows={rates}
                columns={headersConfig}
                pageSize={15}
                rowsPerPageOptions={[15]}
                rowHeight={30}
                headerHeight={40}
                components={{ Toolbar: GridToolbar }}
              />
            </div>
          </div>
          {/* /.container-fluid */}
        </section>
        {/* /.content */}
      </div>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={handleClose}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert onClose={handleClose} severity={severity} sx={{ width: "100%" }}>
          {alart}
        </Alert>
      </Snackbar>

      <Dialog
        open={openDialog}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

function arrayEquals(a, b) {
  return (
    Array.isArray(a) &&
    Array.isArray(b) &&
    a.length === b.length &&
    a.every((val, index) => val === b[index])
  );
}

export default withRouter(HandoverNewTasks);
